/*bigOperators*/
.bigOperator {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
}

.bigOperator > span {
  display: block;
  font-size: 80%;
}

.bigOperator .top-limit {
  margin-bottom: -0.1ex;
}

.bigOperator .bottom-limit {
  margin-top: -0.5ex;
  font-weight: bolder;
}

.bigOperator .sum-symbol {
  font-size: 1.5em;
  font-weight: bolder;
}

.bigOperator .symbol-ico {
  font-size: 2em;
  font-weight: normal;
}

.bigOperator .top-limit-ico {
  margin-bottom: -0.3ex;
  font-weight: bolder;

}

.bigOperator .bottom-limit-ico {
  margin-top: -0.3ex;
  font-weight: bolder;

}
.slate-bigop > .slate-box:nth-child(1) {
  font-size: x-small;
  font-family: Cambria, Georgia, serif; font-style: normal; font-variant: normal; 
}

.slate-bigop > div{
  font-family: Cambria, Georgia, serif; font-style: normal; font-variant: normal; 
  font-size: 1.5em; 
}
.slate-bigop > .slate-box:nth-child(3) {
  font-size: x-small;
  font-family: Cambria, Georgia, serif; font-style: normal; font-variant: normal; 
}
/*End of bigOperators*/
